.inputClass {
    resize: none;
    outline: none;
    border: 1px solid black;
    border-radius: 4px;
    padding: 6px;
    height: 20px;
    width: 300px;
    font-family: "Century Schoolbook";
    font-size: 12px;
}


.inputClass:focus{
    background: #fbfbfb;
    transition: .5s;
}
